.navigation {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.navigation__list {
	@include reset-list;

	display: flex;
	flex-wrap: wrap;
}

.navigation__item {
	margin-right: 30px;

	&:last-child {
		margin-right: 0;
	}
}

.navigation__link {
	color: #000;
	font-weight: 500;
	line-height: 24px;
	font-size: 16px;
}

