.premiere {
	grid-column: 1 / -1;
	grid-row: 1 / 2;
}

.premiere__title {
	color: #1f1e1c;
	font-size: 46px;
	line-height: 69px;
	font-weight: inherit;
	margin: 70px 0 30px 0;
}

.premiere__figure {
	margin: 0;
}

.premiere__info {
	display: flex;
	align-items: center;
	margin-bottom: 50px;
}

.premiere__date,
.premiere__author,
.premiere__photographer {
	color: #1f1e1c;
}

.premiere__date {
	margin-right: 50px;
}

.premiere__author {
	margin: 0 50px 0 0;
}

.premiere__photographer {
	margin: 0;
}
