.article {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: repeat(5, auto);
	grid-row-gap: 70px;
}

.article__section {
	grid-column: 1 / -1;

	display: flex;
	justify-content: space-between;

	&--1 {
		grid-row: 2 / 3;

		.article__text {
			width: 30.6%;
		}

		.article__figure {
			width: 65.32%;
			display: flex;
			justify-content: space-between;

			.article__picture {
				width: 47%;
			}

			.article__info {
				width: 47%;
			}
		}
	}

	&--2 {
		grid-row: 3 / 4;

		.article__text {
			width: 30.6%;
		}

		.article__figure {
			width: 65.32%;
		}

		.article__picture {
			margin-bottom: 30px;
		}
	}

	&--3 {
		grid-row: 4 / 5;

		.article__figure {
			width: 30.6%;
		}

		.article__figure:first-child {
			width: 65.32%;
		}

		.article__picture {
			margin-bottom: 30px;
		}
	}

	&--4 {
		grid-row: 5 / -1;

		.article__text {
			width: 30.6%;
		}

		.article__figure {
			width: 30.6%;
		}

		.article__picture {
			margin-bottom: 30px;
		}
	}
}

.article__figure {
	margin: 0;
}

.article__shown,
.article__photographer {
	margin: 0;
	font-size: 16px;
	line-height: 24px;
}

.article__shown {
	margin-bottom: 10px;
}

.article__text {
	margin: 0;
}